import axios from 'axios';

//this is our base id
//const USER_API_BASE_URL = 'http://localhost:8080';

class ApiService {

	
    //provide a 
    login(user) {
        return axios.post('/login', user);
    }

    registerUser(user) {
        return axios.post('/register', user);
    }

    getAllTodos() {
        const TOKEN = localStorage.token;
       //console.log(TOKEN);
        return axios.get('/api/v1/todo', {headers:{
            'Authorization': TOKEN
        }});
    }

    postTodo(todo) {
        const TOKEN = localStorage.token;
        return axios.post("/api/v1/todo", todo, {headers:{
            'Authorization': TOKEN
        }});
    }


}

export default new ApiService();