import React, { Component } from 'react'
import api from "../service/api"

//We're gonna use this to make a get to grab all the todos, and then we can list them
class ListTodo extends Component{

    constructor(props){
        super(props);
        this.state={
         todos:[]
        }
      }

    AddUserButton = (e) =>{
        this.props.history.push("/addUser");
    }

    AddTodoButton = (e) =>{
        this.props.history.push("/addTodo");
    } 

    componentDidMount() {
        this.reloadTodoList();
    }

    reloadTodoList() {
        api.getAllTodos()
            .then((res) => {
                this.setState({todos: res.data})
            });
    }

        render(){
          return(
            <div>
            <h2 className="text-center">Your To-do List!</h2>
           
            <table className="table table-striped">
                <thead>
                    <tr>
                        <th className="hidden">To-do ID</th>
                        
                        <th>To-do Date</th>
                        <th>To-do Task</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        this.state.todos.map(
                    todo =>
                                <tr key={todo.todoId}>
                                    <td>{todo.todoId}</td>
                                    <td>{todo.todoDate}</td>
                                    <td>{todo.todoName}</td>
                                </tr>
                        )
                    }
                </tbody>
            </table>

               <div>
                <button onClick={this.AddUserButton}>Add User</button>                
                <button onClick={this.AddTodoButton}>Add Todo Task</button></div>     
        </div>
       ); }
          

}


export default ListTodo;