import React, { Component } from 'react'
import api from "../service/api"

class AddTodo extends Component{

    constructor(props){
        super(props);
        this.state={
            todoName:"",
            todoDate:""
        }
    }
    
    AddTodo = (e) => {
         e.preventDefault();
         let todo = {todoName: this.state.todoName, todoDate: new Date(this.state.todoDate).getTime()};
         //console.log(todo.todoDate)
         api.postTodo(todo)
             .then(res => {
                 console.log(res)
                 this.props.history.push('/listTodo');
             }).catch((e)=>
             {
                 console.log("Registration failed as", this.state.todoName);
                 this.props.history.push("/error");
             }
             );
     }
 
     onChange = (e) =>
     this.setState({ [e.target.name]: e.target.value });
 

    render(){
        return(
        <div className="AddTodo">
            <h1>Add a Todo Item!</h1>
            <form onSubmit={this.AddTodo}>
                <div className="form-group">
                    <label>Todo Task:</label>
                    <input type="text" placeholder="todoName" name="todoName" className="form-control" value={this.state.todoName} onChange={this.onChange} required/>
                </div>
                <div className="form-group">
                    <label>Due Date:</label>
                    <input type="date" placeholder="todoDate" name="todoDate" className="form-control" value={this.state.todoDate} onChange={this.onChange} required/>
                </div>
                <input type="submit" value="Add Task!"></input>
                 
            </form>
        </div>
        )
        }


}



export default AddTodo;