import React, { Component } from 'react'
import api from "../service/api"

class Login extends Component{

  
    constructor(props){
        super(props);
        this.state={
            username :"",
            password:""
        }
    }

    login = (e) => {
       // alert('A form was submitted: ' + JSON.stringify(this.state));
        e.preventDefault();
        let user = {username: this.state.username, password: this.state.password};
       // console.log("there should be a res here!!!")
        api.login(user)
            .then(res => {
                //console.log("there is a res here!!!")
                console.log(res)
               // this.setState({respHeaders : res.headers.authorization});
                this.setStorageAdmin(res.headers.authorization, this.state.username);
                this.props.history.push('/listTodo');
            }).catch((e)=>
            {
                console.log("Login failed as", this.state.username);
                this.props.history.push("/error");
            }
            );
        //return this.state.respHeaders;
    }

    onChange = (e) =>
    this.setState({ [e.target.name]: e.target.value });

    setStorageAdmin = (token, username) => {
        if (token) {
          localStorage.setItem("token", token)
          //this.setState({ token }, () => this.setUserName(username));
        } else {
          alert("Invalid Credentials")
        }
      }

      AddUserButton = (e) =>{
        this.props.history.push("/addUser");
    }

    render(){
        return(
        <div className="LoginObj">
            <form onSubmit={this.login}>
                <div className="form-group">
                    <label>User Name:</label>
                    <input type="text" placeholder="username" name="username" className="form-control" value={this.state.username} onChange={this.onChange}/>
                </div>

                <div className="form-group">
                    <label>Password:</label>
                    <input type="password" placeholder="password" name="password" className="form-control" value={this.state.password} onChange={this.onChange}/>
                </div>
                <input type="submit" value="Login"></input>
                 
                </form>
                
                <button onClick={this.AddUserButton}>Add User</button>    
        </div>
        )
    }

}



export default Login;