import React, { Component } from 'react'


class Error extends Component{

      
    constructor(props){
        super(props);
        this.state={
            
        }
    }


    loginAgain = () =>{
        this.props.history.push("/");
    }    

    render(){
        return(
            <div>
                <p>There was an error on login!</p>
                {/* <Link to="/login" /> */}
                <button onClick={this.loginAgain}>Login Again!</button>
            </div>
        )
    }

}


export default Error;